export default {
	data() {
		return {
			item:null
		};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad(opt) {
		if(opt.item){
			console.log(JSON.parse(opt.item))
			this.item=JSON.parse(opt.item)
		}
	}
}
